import React, { Component } from "react";
import { Button, Divider, Form, Grid, Segment } from "semantic-ui-react";

import { Link } from "react-router-dom";

import { GoogleLogin } from "react-google-login";
import {
  Card,
  CardBody,
  CardGroup,
  Col,
  Container,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Row
} from "reactstrap";
import { getUserDetails } from "../../../services/userService";

class Login extends Component {
  username = React.createRef();
  constructor(props) {
    super(props);
    this.state = {
      account: {
        username: "",
        password: ""
      }
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange = ({ currentTarget: input }) => {
    const account = {
      ...this.state.account
    };
    account[input.name] = input.value;
    this.setState({
      account
    });
    console.log(input.value);
  };

  handleSubmit = e => {
    e.preventDefault();
    const account = this.state.account;

    // login(account.username, account.password).then(res => {
    //   const r = JSON.parse(res.text);
    //   if (r.status == "Successful") {
    //     localStorage.setItem("user", r.names);
    //     window.location = "/";
    //   }
    //   console.log(r);
    // });
    // window.location = "/";
  };

  successGoogle = response => {
    console.log(response);
    const account = this.state.account;

    const familyName = response.profileObj.familyName;
    const imageUrl = response.profileObj.imageUrl;
    const username = response.profileObj.email;
    localStorage.setItem("user", familyName);
    localStorage.setItem("imageUrl", imageUrl);
    localStorage.setItem("username", username);

    getUserDetails(username).then(d => {
      console.log(d.data);
      if (d.data) {
        localStorage.setItem("roleId", d.data.role.id);
        window.location = "/";
      } else {
        localStorage.setItem("roleId", 10);
        window.location = "/";
      }
    });
  };

  failureGoogle = response => {
    console.log(response);
  };

  render() {
    const account = this.state.account;
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              {/* <CardGroup>
                                                <Card className="p-4">
                                                  <CardBody>
                                                    

                                                    <br />
                                                    <h4 className="text-center">-or-</h4>

                                                    <Form onSubmit={this.handleSubmit}>
                                                      <p className="text-muted">Sign In to your account</p>
                                                      <InputGroup className="mb-3">
                                                        <InputGroupAddon addonType="prepend">
                                                          <InputGroupText>
                                                            <i className="icon-user" />
                                                          </InputGroupText>
                                                        </InputGroupAddon>
                                                        <Input
                                                          type="text"
                                                          name="username"
                                                          onChange={this.handleChange}
                                                          value={account.username}
                                                          ref={this.username}
                                                          placeholder="Username"
                                                          autoComplete="username"
                                                        />
                                                      </InputGroup>
                                                      <InputGroup className="mb-4">
                                                        <InputGroupAddon addonType="prepend">
                                                          <InputGroupText>
                                                            <i className="icon-lock" />
                                                          </InputGroupText>
                                                        </InputGroupAddon>
                                                        <Input
                                                          type="password"
                                                          name="password"
                                                          onChange={this.handleChange}
                                                          value={account.password}
                                                          placeholder="Password"
                                                          autoComplete="current-password"
                                                        />
                                                      </InputGroup>
                                                      <Row>
                                                        <Col xs="6">
                                                          <Button color="primary" className="px-4">
                                                            Login
                                                          </Button>
                                                        </Col>
                                                        <Col xs="6" className="text-right">
                                                          <Button color="link" className="px-0">
                                                            Forgot password?
                                                          </Button>
                                                        </Col>
                                                      </Row>
                                                    </Form>
                                                  </CardBody>
                                                </Card>
                                                <Card
                                                  className="text-white bg-primary py-5 d-md-down-none"
                                                  style={{ width: "44%" }}
                                                >
                                                  <CardBody className="text-center">
                                                    <div>
                                                      <h2>Sign up</h2>
                                                      <p>
                                                        Lorem ipsum dolor sit amet, consectetur adipisicing
                                                        elit, sed do eiusmod tempor incididunt ut labore et
                                                        dolore magna aliqua.
                                                      </p>
                                                      <Link to="/register">
                                                        <Button
                                                          color="primary"
                                                          className="mt-3"
                                                          active
                                                          tabIndex={-1}
                                                        >
                                                          Register Now!
                                                        </Button>
                                                      </Link>
                                                    </div>
                                                  </CardBody>
                                                </Card>
                                              </CardGroup> */}
              <Segment placeholder>
                <Grid columns={2} relaxed="very" stackable>
                  <Grid.Column>
                    <Form>
                      <Form.Input
                        icon="user"
                        iconPosition="left"
                        label="Username"
                        placeholder="Username"
                        name="username"
                        onChange={this.handleChange}
                        value={account.username}
                        placeholder="Username"
                        autoComplete="username"
                      />
                      <Form.Input
                        icon="lock"
                        iconPosition="left"
                        label="Password"
                        type="password"
                        name="password"
                        onChange={this.handleChange}
                        value={account.password}
                        placeholder="Password"
                        autoComplete="current-password"
                      />

                      <Button content="Login" primary />
                    </Form>
                  </Grid.Column>
                  <Grid.Column verticalAlign="middle">
                    <GoogleLogin
                      className="align-self-center"
                      clientId={
                        "917824881969-onm7jit8qsnm0ti2r25n568i7v4v1snb.apps.googleusercontent.com"
                      }
                      onSuccess={this.successGoogle}
                      onFailure={this.failureGoogle}
                    >
                      <span> Login with Google </span>
                    </GoogleLogin>
                  </Grid.Column>
                </Grid>
                <Divider vertical> Or </Divider>
              </Segment>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Login;
